import React, { useState } from "react";
import logo from "../../assets/black_logo.webp";
import Resume from "../../assets/Sujoy Ghosal.pdf";
const Header = ({ textEnter, textLeave }) => {
  const [menuActive, setmenuActive] = useState(false);
  const [activeLink, setActiveLink] = useState('home');
  return (
    <header className="fixed top-0 left-0 py-4">
      <nav className="px-6 md:px-32  flex justify-between items-center">
        <a
          href="#"
          className="logo flex justify-center items-center"
          onMouseEnter={textEnter}
          onMouseLeave={textLeave}
         role="logo"
        >
          <img src={logo} alt="" className="opacity-40 w-5/12 sm:w-6/12 " />
        </a>

        <div className="menu flex items-center">
          <div
            className="menubar"
            onMouseEnter={textEnter}
            onMouseLeave={textLeave}
            onClick={() =>{ setmenuActive(!menuActive)}}
           
          >
            <div className="menu-line rounded-2xl w-7 h-1"></div>
            <div className="menu-line rounded-2xl w-7 h-1 mt-1.5"></div>
            <div className="menu-line rounded-2xl w-4 h-1 mt-1.5"></div>
          </div>
          <div
            className={
              menuActive
                ? "navbar fixed bottom-14 px-8 py-5 rounded-full"
                : "navbar fixed -bottom-24 px-8 py-5 rounded-full"
            }
          >
            <ul
              className="flex justify-between items-center"
              onMouseEnter={textEnter}
              onMouseLeave={textLeave}
            >
              <li >
                <a href="#" className={activeLink === 'home' ? 'active' : ''} onClick={() => setActiveLink('home')}>
                  <svg
                    stroke="currentColor"
                    fill="none"
                    stroke-width="2"
                    viewBox="0 0 24 24"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    height="1.5em"
                    width="1.5em"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <desc></desc>
                    <path stroke="none" d="M0 0h24v24H0z" fill="none"></path>
                    <path d="M19 8.71l-5.333 -4.148a2.666 2.666 0 0 0 -3.274 0l-5.334 4.148a2.665 2.665 0 0 0 -1.029 2.105v7.2a2 2 0 0 0 2 2h12a2 2 0 0 0 2 -2v-7.2c0 -.823 -.38 -1.6 -1.03 -2.105"></path>
                    <path d="M16 15c-2.21 1.333 -5.792 1.333 -8 0"></path>
                  </svg>
                </a>
              </li>
              <li>
                <a href="#about" className={activeLink === 'about' ? 'active' : ''} onClick={() => setActiveLink('about')}>
                  <svg
                    stroke="currentColor"
                    fill="currentColor"
                    stroke-width="0"
                    viewBox="0 0 24 24"
                    height="1.5em"
                    width="1.5em"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path d="M12 2a5 5 0 1 0 5 5 5 5 0 0 0-5-5zm0 8a3 3 0 1 1 3-3 3 3 0 0 1-3 3zm9 11v-1a7 7 0 0 0-7-7h-4a7 7 0 0 0-7 7v1h2v-1a5 5 0 0 1 5-5h4a5 5 0 0 1 5 5v1z"></path>
                  </svg>
                </a>
              </li>
              <li>
                <a href="#project" className={activeLink === 'project' ? 'active' : ''} onClick={() => setActiveLink('project')}>
                  <svg
                    stroke="currentColor"
                    fill="currentColor"
                    stroke-width="0"
                    viewBox="0 0 24 24"
                    height="1.5em"
                    width="1.5em"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <g>
                      <path fill="none" d="M0 0h24v24H0z"></path>
                      <path d="M3.161 4.469a6.5 6.5 0 0 1 8.84-.328 6.5 6.5 0 0 1 9.178 9.154l-7.765 7.79a2 2 0 0 1-2.719.102l-.11-.101-7.764-7.791a6.5 6.5 0 0 1 .34-8.826zm1.414 1.414a4.5 4.5 0 0 0-.146 6.21l.146.154L12 19.672l5.303-5.304-3.535-3.535-1.06 1.06a3 3 0 1 1-4.244-4.242l2.102-2.103a4.501 4.501 0 0 0-5.837.189l-.154.146zm8.486 2.828a1 1 0 0 1 1.414 0l4.242 4.242.708-.706a4.5 4.5 0 0 0-6.211-6.51l-.153.146-3.182 3.182a1 1 0 0 0-.078 1.327l.078.087a1 1 0 0 0 1.327.078l.087-.078 1.768-1.768z"></path>
                    </g>
                  </svg>
                </a>
              </li>
              <li>
                <a href="#testimonial" className={activeLink === 'testimonial' ? 'active' : ''} onClick={() => setActiveLink('testimonial')}>
                  <svg
                    stroke="currentColor"
                    fill="currentColor"
                    stroke-width="0"
                    viewBox="0 0 24 24"
                    height="1.5em"
                    width="1.5em"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <g>
                      <path fill="none" d="M0 0h24v24H0z"></path>
                      <path d="M3 3h18a1 1 0 0 1 1 1v16a1 1 0 0 1-1 1H3a1 1 0 0 1-1-1V4a1 1 0 0 1 1-1zm8.126 9H4v7h16v-7h-1.126a4.002 4.002 0 0 1-7.748 0zm0-2a4.002 4.002 0 0 1 7.748 0H20V5H4v5h7.126zM15 13a2 2 0 1 0 0-4 2 2 0 0 0 0 4zm-9 2h2v2H6v-2z"></path>
                    </g>
                  </svg>
                </a>
              </li>
              <li>
                <a href="#contact" className={activeLink === 'contact' ? 'active' : ''} onClick={() => setActiveLink('contact')}>
                  <svg
                    stroke="currentColor"
                    fill="currentColor"
                    stroke-width="0"
                    viewBox="0 0 24 24"
                    height="1.5em"
                    width="1.5em"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path fill="none" d="M0 0h24v24H0V0z"></path>
                    <path d="M20.84 4.22c-.05-.12-.11-.23-.18-.34-.14-.21-.33-.4-.54-.54-.11-.07-.22-.13-.34-.18-.24-.1-.5-.16-.78-.16h-1V1h-2v2H8V1H6v2H5c-.42 0-.8.13-1.12.34-.21.14-.4.33-.54.54-.07.11-.13.22-.18.34-.1.24-.16.5-.16.78v14a2 2 0 002 2h14c.28 0 .54-.06.78-.16.12-.05.23-.11.34-.18.21-.14.4-.33.54-.54.21-.32.34-.71.34-1.12V5c0-.28-.06-.54-.16-.78zM5 19V5h14v14H5zm7-6.12c-2.03 0-6 1.08-6 3.58V18h12v-1.53c0-2.51-3.97-3.59-6-3.59zM8.31 16c.69-.56 2.38-1.12 3.69-1.12s3.01.56 3.69 1.12H8.31zM12 12c1.65 0 3-1.35 3-3s-1.35-3-3-3-3 1.35-3 3 1.35 3 3 3zm0-4c.55 0 1 .45 1 1s-.45 1-1 1-1-.45-1-1 .45-1 1-1z"></path>
                  </svg>
                </a>
              </li>
            </ul>
          </div>
          <a
            href={Resume}
            download={true}
            className="downloadCv"
            onMouseEnter={textEnter}
            onMouseLeave={textLeave}
          >
            Download CV
          </a>
        </div>
      </nav>
    </header>
  );
};

export default Header;
